import React, { useEffect, useState } from "react";
import cc from "classcat";
import { IconType } from "./Icon.types";

const IconSize = {
  Sm: "16px",
  Md: "20px",
  Lg: "24px",
};

export interface IconProps {
  className?: string;
  icon: IconType;
  size?: "sm" | "md" | "lg";
}

const Icon = ({ icon, size = "md" }: IconProps) => {
  const [LazyIcon, setLazyIcon] = useState(null);

  // Lazy load icon
  useEffect(() => {
    const importIcon = async () => {
      try {
        const { default: importedIcon } = await import(
          `./assets/icons/${icon}.svg`
        );
        setLazyIcon(importedIcon);
      } catch (error) {
        console.warn(`Failed to load icon: \`${icon}\``);
      }
    };
    importIcon();
  }, [icon]);

  if (!LazyIcon) {
    return null;
  }

  // TODO: Figure out how to remove <span> wrapper

  return <span className={cc("icon", `icon--${size}`)}>{LazyIcon}</span>;
};

export { Icon };
